<template>
  <div>
    <el-button type="success" plain @click="ClearCompleted"
      >Clear Completed</el-button
    >
  </div>
</template>

<script>
export default {
  name: 'todo-clear-completed',
  methods: {
    ClearCompleted() {
      this.$store.commit('ClearCompleted')
    }
  }
}
</script>


