<template>
  <div>
    <el-checkbox v-model="checkAll" /><label for="checkAll">checkAll</label>
  </div>
</template>

<script>
export default {
  name: 'todo-check-all',
  computed: {
    checkAll: {
      get: function () {
        return this.$store.getters.anyRemaining
      },
      set: function (newValue) {
        this.$store.commit('checkAllTodos', newValue)
      }
    }
  }
}
</script>


